// pages / dashboard / payment / payment-history

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link, {navigateTo} from "gatsby-link";

// Styles
// import styles from '../../../style/preloader.module.css';

// Components
// import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';
// import ChatMessage from '../../../components/dashboard/conversation/ChatMessage';


class ConversationMessage extends React.Component {
    componentDidMount() {
        $('.dataTables-example').DataTable({
            pageLength: 10,
            responsive: true,
            dom: '<"html5buttons"B>lTfgitp',
            buttons: [
                {
                    extend: 'copy'
                },
                {
                    extend: 'csv'
                },
                {
                    extend: 'excel',
                    title: 'ExampleFile'
                },
                {
                    extend: 'pdf',
                    title: 'ExampleFile'
                },

                {
                    extend: 'print',
                    customize: function(win) {
                        $(win.document.body).addClass('white-bg');
                        $(win.document.body).css('font-size', '10px');

                        $(win.document.body).find('table')
                            .addClass('compact')
                            .css('font-size', 'inherit');
                    }
                }
            ]

        });
    }

    render() {
        if (this.props.user.loggedIn) {

            return (
                <span>

                    {/* Start of Helmet */}
                    <Helmet title="Payment History" meta={[
                        {
                            name: 'description',
                            content: 'Payment History'
                        }, {
                            name: 'keywords',
                            content: 'Payment History'
                        }
                    ]}/>
                    {/* End of Helmet */}


                    {/* Start of Heading */}
                    <div className="row wrapper border-bottom white-bg page-heading">
                        <div className="col-lg-10">
                            <h2>Payment History</h2>
                            <ol className="breadcrumb">
                                <li>
                                    <Link to="/dashboard">Home</Link>
                                </li>
                                <li className="active">
                                    <strong>Payment History List</strong>
                                </li>
                            </ol>
                        </div>
                        <div className="col-lg-2">

                        </div>
                    </div>
                    {/* End of Heading */}


                    {/* Start of Content */}
                    <div className="wrapper wrapper-content animated fadeInRight">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="ibox float-e-margins">
                                    <div className="ibox-title">
                                        <h5>Payment History</h5>
                                        <div className="ibox-tools">
                                            <a className="collapse-link">
                                                    <i className="fa fa-chevron-up"></i>
                                                </a>
                                            <a className="dropdown-toggle" data-toggle="dropdown" href="#">
                                                    <i className="fa fa-wrench"></i>
                                                </a>
                                            <ul className="dropdown-menu dropdown-user">
                                                <li><a href="#">Config option 1</a>
                                                </li>
                                                <li><a href="#">Config option 2</a>
                                                </li>
                                            </ul>
                                            <a className="close-link">
                                                    <i className="fa fa-times"></i>
                                                </a>
                                        </div>
                                    </div>
                                    <div className="ibox-content">
                                        <div className="table-responsive">
                                            <table className="table table-striped table-bordered table-hover dataTables-example">
                                                <thead>
                                                    <tr>
                                                        <th>ID</th>
                                                        <th>Transaction Date</th>
                                                        <th>Bill Type</th>
                                                        <th>Payment Method</th>
                                                        <th>Total Amount (RM)</th>
                                                        <th>Payment Status</th>
                                                        <th>View Payment</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr className="gradeX">
                                                        <td>
                                                            123123123
                                                        </td>
                                                        <td>
                                                            Mar. 5, 2017
                                                        </td>
                                                        <td>
                                                            Electric Bill
                                                        </td>
                                                        <td>
                                                            VISA 1111
                                                        </td>
                                                        <td>
                                                            182.33
                                                        </td>
                                                        <td>
                                                            Completed
                                                        </td>
                                                        <td>
                                                            <Link to="/dashboard/payment-history/view-payment">view</Link>
                                                        </td>
                                                    </tr>
                                                    <tr className="gradeX">
                                                        <td>
                                                            123123123
                                                        </td>
                                                        <td>
                                                            Mar. 5, 2017
                                                        </td>
                                                        <td>
                                                            Maintenance Bill
                                                        </td>
                                                        <td>
                                                            iPay 88
                                                        </td>
                                                        <td>
                                                            182.33
                                                        </td>
                                                        <td>
                                                            Failed
                                                        </td>
                                                        <td>
                                                            <Link to="/dashboard/payment-history/view-payment">view</Link>
                                                        </td>
                                                    </tr>
                                                    <tr className="gradeX">
                                                        <td>
                                                            123123123
                                                        </td>
                                                        <td>
                                                            Mar. 5, 2017
                                                        </td>
                                                        <td>
                                                            Water Bill
                                                        </td>
                                                        <td>
                                                            Master 1111
                                                        </td>
                                                        <td>
                                                            182.33
                                                        </td>
                                                        <td>
                                                            Completed
                                                        </td>
                                                        <td>
                                                            <Link to="/dashboard/payment-history/view-payment">view</Link>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                                {/*
                                                <tfoot>
                                                    <tr>
                                                        <th>ID</th>
                                                        <th>Transaction Date</th>
                                                        <th>Bill Type</th>
                                                        <th>Payment Method</th>
                                                        <th>Total Amount (RM)</th>
                                                        <th>Payment Status</th>
                                                        <th>View Payment</th>
                                                    </tr>
                                                </tfoot>
                                                */}
                                            </table>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* End of Content */}
                </span>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(ConversationMessage);



